.container-tarifa {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 20px;
    margin-bottom: 20px;
    overflow: hidden;
    position: relative;
}

.container-tarifa div,
.container-tarifa a {
    position: relative;
    z-index: 2;
}

.container-tarifa .card {
    background-color: rgba(60, 255, 132, 0.3);
}

#precio {
    border: 4px solid #f66c5d;
    padding: 15px;
    border-radius: 15px;
    margin: 15px auto;
    width: 75%;
    background: aliceblue;
}

@media screen and (min-width: 992px) {
    .container-tarifa::before {
        content: ' ';
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 1;
        opacity: 0.4;
        background-image: url('https://pbs.twimg.com/profile_banners/719539745769058304/1619107589/1500x500');
        background-repeat: no-repeat;
        background-position: top;
        background-size: contain;
    }
}